export function getDate(year, month, day) {
  const now = day ? new Date(year, month, day) : new Date()
  const dateDay = String(now.getDate()).padStart(2, "0")
  const dateMonth = String(now.getMonth() + 1).padStart(2, "0") //January is 0!
  const dateYear = now.getFullYear()
  const today = dateYear + "-" + dateMonth + "-" + dateDay
  return today
}

const notEmpty = value => value != null

export function getAccounts(source) {
  const target = []
  for (let i = 0; i < source.length; i += 4) {
    const portfolio = source[i]
    const account = source[i + 1]
    const type = source[i + 2]
    if (notEmpty(portfolio) && notEmpty(account) && notEmpty(type)) {
      const details = `${portfolio}_${account}_${type}`
      target.push(details)
    }
  }
  return Array.from(new Set(target))
}

export function getExpenses(source) {
  const types = []
  for (let i = 0; i < source.length; i += 4) {
    const details = source[i + 2]
    if (notEmpty(details)) types.push(details)
  }
  return Array.from(new Set(types))
}

export function getGroups(source) {
  const labels = []
  for (let i = 0; i < source.length; i += 4) {
    const details = source[i + 2]
    if (notEmpty(details)) labels.push(details)
  }
  return Array.from(new Set(labels))
}

export function getTargets(source) {
  const targets = new Map()
  for (let i = 0; i < source.length; i += 4) {
    const portfolio = source[i]
    const account = source[i + 1]
    const type = source[i + 2]
    if (notEmpty(portfolio) && notEmpty(account) && notEmpty(type)) {
      const details = `${portfolio}_${account}_${type}`
      const target = parseFloat(source[i + 3])
      if (!targets.has(details)) {
        Number.isNaN(target)
          ? targets.set(details, 0)
          : targets.set(details, target)
      }
    }
  }
  return targets
}

export function getTypes(source, headings) {
  const target = []
  for (let i = 0; i < source.length; i += 2) {
    if (i % headings.length === 2) {
      const details = source[i]
      target.push(details)
    }
  }
  return Array.from(new Set(target))
}
