export const getArray = (id, arr, length) => {
  let i
  let counter = 0
  let rows
  let output = []

  const appendRows = rows => {
    for (i = 0; i < rows; i++) {
      output = output.concat(
        arr(counter, counter + 1, counter + 2, counter + 3)
      )
      counter += 4
    }
  }

  switch (id) {
    case TRANSACTIONS_ID:
      rows = length / TRANSACTIONS_HEADINGS.length
      appendRows(rows)
      break
    case ANALYSIS_ID:
      rows = length / ANALYSIS_HEADINGS.length
      appendRows(rows)
      break
    case STRUCTURE_ID:
      rows = length / STRUCTURE_HEADINGS.length
      appendRows(rows)
      break
    case ACCOUNTS_ID:
      rows = length / ACCOUNTS_HEADINGS.length
      appendRows(rows)
      break
    default:
      rows = length / INCOME_HEADINGS.length
      appendRows(rows)
  }

  return output
}
