import { createStore as reduxCreateStore, bindActionCreators } from "redux"
import * as Tools from "../models/budget/Tools"

import {
  DEFAULT_ANALYSIS,
  DEFAULT_CALCULATOR,
  DEFAULT_PORTFOLIO,
  DEFAULT_SETUP,
  DEFAULT_TRANSACTIONS,
  DEFAULT_ACCOUNTS,
  DEFAULT_EXPENSES,
  DEFAULT_GROUPINGS,
} from "../models/budget/tables"

import {
  UPDATE_SESSION,
  UPDATE_MODE,
  RESIZE_CALCULATOR,
  RESIZE_PORTFOLIO,
  RESIZE_SETUP,
  RESIZE_TRANSACTIONS,
  RESIZE_ANALYSIS,
  UPDATE_CALCULATOR,
  UPDATE_PORTFOLIO,
  UPDATE_SETUP,
  UPDATE_TRANSACTIONS,
  UPDATE_ANALYSIS,
  UPDATE_CHART,
  CLEAR,
  LOAD,
} from "./actionTypes"

const resizeInput = (arr, action) => {
  return action.input
}

const updateArray = (arr, action) => {
  const newState = arr.map(i => i)
  newState[action.index] = action.input
  return newState
}

const monthDiff = (from, to) => {
  return (
    to.getMonth() -
    from.getMonth() +
    12 * (to.getFullYear() - from.getFullYear())
  )
}

const isBlankHelper = value =>
  value === null ||
  value == null ||
  value === "Click to Select Account" ||
  value.toString().trim() === ""

const validRow = (i, ii, iii, iiii) =>
  !(
    isBlankHelper(i) ||
    isBlankHelper(ii) ||
    isBlankHelper(iii) ||
    isBlankHelper(iiii)
  )

const getPeriod = (today, max, setup, transactions) => {
  let start, end
  for (let i = 3; i < setup.length; i += 4) {
    if (validRow(setup[i - 3], setup[i - 2], setup[i - 1], setup[i])) {
      const newDate = new Date(setup[i])
      if (start == null) start = newDate
      if (end == null) end = newDate
      if (
        start.getTime() > newDate.getTime() &&
        newDate.getTime() >= today.getTime()
      ) {
        start = newDate
      }
      if (
        end.getTime() < newDate.getTime() &&
        newDate.getTime() <= max.getTime()
      ) {
        end = newDate
      }
    }
  }
  for (let i = 3; i < transactions.length; i += 4) {
    if (
      validRow(
        transactions[i - 3],
        transactions[i - 2],
        transactions[i - 1],
        transactions[i]
      )
    ) {
      const newDate = new Date(transactions[i])
      if (start == null) start = newDate
      if (end == null) end = newDate
      if (
        start.getTime() > newDate.getTime() &&
        newDate.getTime() >= today.getTime()
      ) {
        start = newDate
      }
      if (
        end.getTime() < newDate.getTime() &&
        newDate.getTime() <= max.getTime()
      ) {
        end = newDate
      }
    }
  }
  return { start, end }
}

const populateAccounts = (months, accounts) => {
  const output = accounts
  for (let i = 0; i <= months; i++) {
    output[2 + i] = new Array(output[0].length).fill(0)
  }
  return output
}

const populateExpenses = (months, expenses) => {
  const output = expenses
  for (let i = 0; i <= months; i++) {
    output[1 + i] = new Array(output[0].length).fill(0)
  }
  return output
}

const setupAccounts = (today, max, start, setup, accounts, accountsHelper) => {
  const output = accounts
  for (let i = 0; i < setup.length; i += 4) {
    const source = setup[i]
    const target = setup[i + 1]
    const value = setup[i + 2]
    const date = setup[i + 3]
    if (validRow(source, target, value, date)) {
      const sourceIndex = accountsHelper.get(source)
      const targetIndex = accountsHelper.get(target)
      const newDate = new Date(date)
      if (monthDiff(today, newDate) >= 0 && monthDiff(newDate, max) >= 0) {
        const dateIndex = monthDiff(start, newDate) + 2
        const float = parseFloat(value)
        output[dateIndex][sourceIndex] -= float
        output[dateIndex][targetIndex] += float
      }
    }
  }
  return output
}

const addExpenses = (
  today,
  max,
  start,
  transactions,
  accounts,
  accountsHelper
) => {
  const output = accounts
  for (let i = 0; i < transactions.length; i += 4) {
    const account = transactions[i]
    const amount = transactions[i + 1]
    const type = transactions[i + 2]
    const date = transactions[i + 3]

    if (validRow(account, amount, type, date)) {
      const accountIndex = accountsHelper.get(account)
      const newDate = new Date(date)

      if (monthDiff(today, newDate) >= 0 && monthDiff(newDate, max) >= 0) {
        const dateIndex = monthDiff(start, newDate) + 2
        const float = parseFloat(amount)
        output[dateIndex][accountIndex] -= float
      }
    }
  }
  return output
}

const addTransactions = (
  today,
  max,
  start,
  transactions,
  expenses,
  expensesHelper
) => {
  const output = expenses
  for (let i = 0; i < transactions.length; i += 4) {
    const account = transactions[i]
    const amount = transactions[i + 1]
    const type = transactions[i + 2]
    const date = transactions[i + 3]

    if (validRow(account, amount, type, date)) {
      const expenseIndex = expensesHelper.get(type)
      const newDate = new Date(date)

      if (monthDiff(today, newDate) >= 0 && monthDiff(newDate, max) >= 0) {
        const dateIndex = monthDiff(start, newDate) + 1
        const float = parseFloat(amount)
        output[dateIndex][expenseIndex] -= float
      }
    }
  }
  return output
}

const normaliseAccounts = accounts => {
  const output = accounts
  if (output.length > 3) {
    for (let i = 3; i < output.length; i++) {
      const previousState = output[i - 1]
      const newState = output[i].map((value, key) => value + previousState[key])
      output[i] = newState
    }
  }
  return output
}

const normaliseExpenses = expenses => {
  const output = expenses
  if (output.length > 2) {
    for (let i = 2; i < output.length; i++) {
      const previousState = output[i - 1]
      const newState = output[i].map((value, key) => value + previousState[key])
      output[i] = newState
    }
  }
  return output
}

const DEFAULT_DATES = () => {
  const today = new Date(Tools.getDate(2020, 0, 1))
  const max = new Date(
    today.getFullYear() + 2,
    today.getMonth(),
    today.getDate()
  )
  return { today, max }
}

const generateAccounts = (portfolio, setup, transactions) => {
  const dates = DEFAULT_DATES()
  const today = dates.today
  const max = dates.max
  let start = undefined,
    end = undefined
  let output = []
  output.push(Tools.getAccounts(portfolio))
  const targets = Tools.getTargets(portfolio)
  output[1] = output[0].map(a => targets.get(a))
  const accountsHelper = new Map()
  output[0].map((account, key) => accountsHelper.set(account, key))
  const period = getPeriod(today, max, setup, transactions)
  start = period.start
  end = period.end
  if (start == null) {
    start = today
    end = today
  }
  const months = monthDiff(start, end)
  output = populateAccounts(months, output)
  output = setupAccounts(today, max, start, setup, output, accountsHelper)
  output = addExpenses(today, max, start, transactions, output, accountsHelper)
  output = normaliseAccounts(output)
  return output
}

const generateExpenses = transactions => {
  const dates = DEFAULT_DATES()
  const today = dates.today
  const max = dates.max
  let start = undefined,
    end = undefined
  let output = []
  output.push(Tools.getExpenses(transactions))
  const expensesHelper = new Map()
  output[0].map((expense, key) => expensesHelper.set(expense, key))
  const period = getPeriod(today, max, transactions, transactions)
  start = period.start
  end = period.end
  if (start == null) {
    start = today
    end = today
  }
  const months = monthDiff(start, end)
  output = populateExpenses(months, output)
  output = addTransactions(
    today,
    max,
    start,
    transactions,
    output,
    expensesHelper
  )
  output = normaliseExpenses(output)
  return output
}

const populateGroups = (size, groupings) => {
  let output = groupings
  for (let i = 1; i < size; i++) {
    output[i] = new Array(output[0].length).fill(0)
  }
  return output
}

const addGroups = (
  groupings,
  expenses,
  analysis,
  groupsHelper,
  expensesHelper
) => {
  let output = groupings
  const outputHelper = []
  for (let i = 2; i < analysis.length; i += 4) {
    const label = analysis[i]
    const transactions = analysis[i + 1]
    if (groupsHelper.has(label)) {
      const labelIndex = groupsHelper.get(label)
      if (outputHelper[labelIndex] == null) outputHelper[labelIndex] = []
      outputHelper[labelIndex] = Array.from(
        new Set(outputHelper[labelIndex].concat(transactions))
      )
    }
  }

  for (let i = 1; i < output.length; i++) {
    for (let j = 0; j < outputHelper.length; j++) {
      let value = 0
      let labels = outputHelper[j]
      for (let k = 0; k < labels.length; k++) {
        const type = expensesHelper.get(labels[k])
        if (type != null) value += parseFloat(expenses[i][type])
      }
      output[i][j] = value
    }
  }
  return output
}

const generateGroupings = (expenses, analysis) => {
  let output = []
  output.push(Tools.getGroups(analysis))
  const groupsHelper = new Map()
  const expensesHelper = new Map()
  expenses[0].map((expense, key) => expensesHelper.set(expense, key))
  output[0].map((group, key) => groupsHelper.set(group, key))
  output = populateGroups(expenses.length, output)
  output = addGroups(output, expenses, analysis, groupsHelper, expensesHelper)
  return output
}

// reducer (pure function)
const reducer = (state, action) => {
  switch (action.type) {
    case UPDATE_SESSION:
      return { ...state, session: action.text }
    case UPDATE_MODE:
      return { ...state, smartMode: !state.smartMode }
    case RESIZE_CALCULATOR:
      return { ...state, calculator: resizeInput(state.calculator, action) }
    case RESIZE_PORTFOLIO:
      return { ...state, portfolio: resizeInput(state.portfolio, action) }
    case RESIZE_SETUP:
      return { ...state, setup: resizeInput(state.setup, action) }
    case RESIZE_TRANSACTIONS:
      return { ...state, transactions: resizeInput(state.transactions, action) }
    case RESIZE_ANALYSIS:
      return { ...state, analysis: resizeInput(state.analysis, action) }
    case UPDATE_CALCULATOR:
      return { ...state, calculator: updateArray(state.calculator, action) }
    case UPDATE_PORTFOLIO:
      const newPortfolio = updateArray(state.portfolio, action)
      return {
        ...state,
        portfolio: newPortfolio,
        accounts: generateAccounts(
          newPortfolio,
          state.setup,
          state.transactions
        ),
      }
    case UPDATE_SETUP:
      const newSetup = updateArray(state.setup, action)
      return {
        ...state,
        setup: newSetup,
        accounts: generateAccounts(
          state.portfolio,
          newSetup,
          state.transactions
        ),
      }
    case UPDATE_TRANSACTIONS:
      const newTransactions = updateArray(state.transactions, action)
      const expenses = generateExpenses(newTransactions)
      return {
        ...state,
        accounts: generateAccounts(
          state.portfolio,
          state.setup,
          newTransactions
        ),
        transactions: newTransactions,
        expenses,
        groupings: generateGroupings(expenses, state.analysis),
      }
    case UPDATE_ANALYSIS:
      const newAnalysis = updateArray(state.analysis, action)
      return {
        ...state,
        analysis: newAnalysis,
        groupings: generateGroupings(state.expenses, newAnalysis),
      }
    case UPDATE_CHART:
      let temp
      const newleft = [...state.left]
      newleft[action.index] = action.left
      const newright = [...state.right]
      newright[action.index] = action.right
      const newrefAreaLeft = [...state.refAreaLeft]
      newrefAreaLeft[action.index] = action.refAreaLeft
      const newrefAreaRight = [...state.refAreaRight]
      newrefAreaRight[action.index] = action.refAreaRight
      const newtop = [...state.top]
      newtop[action.index] = action.top
      const newbottom = [...state.bottom]
      newbottom[action.index] = action.bottom
      const newanimation = [...state.animation]
      newanimation[action.index] = action.animation
      return {
        ...state,
        left: newleft,
        right: newright,
        refAreaLeft: newrefAreaLeft,
        refAreaRight: newrefAreaRight,
        top: newtop,
        bottom: newbottom,
        animation: newanimation,
      }
    case CLEAR:
      return {
        ...state,
        calculator: DEFAULT_CALCULATOR,
        portfolio: DEFAULT_PORTFOLIO,
        setup: DEFAULT_SETUP,
        transactions: DEFAULT_TRANSACTIONS,
        analysis: DEFAULT_ANALYSIS,
        accounts: DEFAULT_ACCOUNTS,
        expenses: DEFAULT_EXPENSES,
        groupings: DEFAULT_GROUPINGS,
      }
    case LOAD:
      console.log(action)
      return {
        ...state,
        calculator: action.newState.calculator,
        portfolio: action.newState.portfolio,
        setup: action.newState.setup,
        transactions: action.newState.transactions,
        analysis: action.newState.analysis,
        accounts: action.newState.accounts,
        expenses: action.newState.expenses,
        groupings: action.newState.groupings,
      }
    default:
      return state
  }
}

// redo reset

const initialState = {
  session: "Hello!",
  smartMode: true,
  calculator: DEFAULT_CALCULATOR,
  portfolio: DEFAULT_PORTFOLIO,
  setup: DEFAULT_SETUP,
  transactions: DEFAULT_TRANSACTIONS,
  analysis: DEFAULT_ANALYSIS,
  accounts: DEFAULT_ACCOUNTS,
  expenses: DEFAULT_EXPENSES,
  groupings: DEFAULT_GROUPINGS,
  left: ["dataMin", "dataMin", "dataMin"],
  right: ["dataMax + 1", "dataMax  + 1", "dataMax  + 1"],
  refAreaLeft: ["", "", ""],
  refAreaRight: ["", "", ""],
  top: ["dataMax + 1", "dataMax + 1", "dataMax + 1"],
  bottom: ["dataMin - 1", "dataMin - 1", "dataMin - 1"],
  animation: [true, true, true],
}

const store = () => reduxCreateStore(reducer, initialState)

export default store
