export const UPDATE_SESSION = `UPDATE_SESSION`
export const UPDATE_MODE = `UPDATE_MODE`
export const RESIZE_CALCULATOR = `RESIZE_CALCULATOR`
export const UPDATE_CALCULATOR = `UPDATE_CALCULATOR`
export const RESIZE_PORTFOLIO = `RESIZE_PORTFOLIO`
export const UPDATE_PORTFOLIO = `UPDATE_PORTFOLIO`
export const RESIZE_SETUP = `RESIZE_SETUP`
export const UPDATE_SETUP = `UPDATE_SETUP`
export const RESIZE_TRANSACTIONS = `RESIZE_TRANSACTIONS`
export const UPDATE_TRANSACTIONS = `UPDATE_TRANSACTIONS`
export const RESIZE_ANALYSIS = `RESIZE_ANALYSIS`
export const UPDATE_ANALYSIS = `UPDATE_ANALYSIS`
export const UPDATE_CHART = `UPDATE_CHART`
export const CLEAR = `CLEAR`
export const LOAD = `LOAD`

// export const UPDATE_ACCOUNTS = `UPDATE_ACCOUNTS`
// export const UPDATE_EXPENSES = `UPDATE_EXPENSES`
// export const UPDATE_GROUPINGS = `UPDATE_GROUPINGS`

