// export const updateGroupings = groupings => ({
//   type: UPDATE_GROUPINGS,
//   groupings,
// })

export const load = newState => ({ type: LOAD, newState })

export const clear = () => ({ type: CLEAR })

export const updateChart = (
  index,
  left,
  right,
  refAreaLeft,
  refAreaRight,
  top,
  bottom,
  animation
) => ({
  type: UPDATE_CHART,
  index,
  left,
  right,
  refAreaLeft,
  refAreaRight,
  top,
  bottom,
  animation,
})

export const updateSession = text => ({ type: UPDATE_SESSION, text })
export const updateMode = smartMode => ({ type: UPDATE_MODE, smartMode })

export const resize = (type, input) => ({ type, input })

export const updateTable = (type, input, index) => ({
  type,
  input,
  index,
})

export const resizeCalculator = input => resize(RESIZE_CALCULATOR, input)

export const updateCalculator = (input, index) =>
  updateTable(UPDATE_CALCULATOR, input, index)

export const resizePortfolio = input => resize(RESIZE_PORTFOLIO, input)

export const updatePortfolio = (input, index) =>
  updateTable(UPDATE_PORTFOLIO, input, index)

export const resizeSetup = input => resize(RESIZE_SETUP, input)

export const updateSetup = (input, index) =>
  updateTable(UPDATE_SETUP, input, index)

export const resizeTransactions = input => resize(RESIZE_TRANSACTIONS, input)

export const updateTransactions = (input, index) =>
  updateTable(UPDATE_TRANSACTIONS, input, index)

export const resizeAnalysis = input => resize(RESIZE_ANALYSIS, input)

export const updateAnalysis = (input, index) =>
  updateTable(UPDATE_ANALYSIS, input, index)
